import React, {  Suspense, lazy } from "react";
import { BrowserRouter as Router, Routes, Route } from "react-router-dom";

const CapCaoSu = lazy(()=>import("./service/CapCaoSu"))
const CapChongChay = lazy(()=>import("./service/CapChongChay"))
const CapDienAp = lazy(()=>import("./service/CapDienAp"))
const CapDongNhomBoc = lazy(()=>import("./service/CapDongNhomBoc"))
const CapDongNhomTran = lazy(()=>import("./service/CapDongNhomTran"))
const CapTrungThe = lazy(()=>import("./service/CapTrungThe"))
const CapVanXoan = lazy(()=>import("./service/CapVanXoan"))
const DienDanDung = lazy(()=>import("./service/DienDanDung"))
const SoiNhomTran = lazy(()=>import("./service/SoiNhomTran"))


const HomeOne = lazy(() => import("./home/HomeOne"));
const About = lazy(() => import("./pages/About"));
const ServiceDetailsLeftSidebar = lazy(() => import("./service/ServiceDetailsLeftSidebar"));
const ServiceDetailsRightSidebar = lazy(() => import("./service/ServiceDetailsRightSidebar"));
const Projects = lazy(() => import("./project/Projects"));
const ProjectDetails = lazy(() => import("./project/ProjectDetails"));
const BlogLeftSidebar = lazy(() => import("./blog/BlogLeftSidebar"));
const BlogRightSidebar = lazy(() => import("./blog/BlogRightSidebar"));
const BlogDetailsLeftSidebar = lazy(() => import("./blog/BlogDetailsLeftSidebar"));
const BlogDetailsRightSidebar = lazy(() => import("./blog/BlogDetailsRightSidebar"));
const Contact = lazy(() => import("./pages/Contact"));
const NoMAtch = lazy(() => import("./pages/404"));

const App = () => {
  return (
    <Router>
        <Suspense fallback={<div />}>
            <Routes>
                <Route path={`${process.env.PUBLIC_URL}/`} element={<HomeOne />} />
                <Route path={`${process.env.PUBLIC_URL}/about-us`} element={<About />} />
                <Route path={`${process.env.PUBLIC_URL}/services`} element={<CapTrungThe />} />
                <Route path={`${process.env.PUBLIC_URL}/services/cap-trung-the`} element={<CapTrungThe />} />
                <Route path={`${process.env.PUBLIC_URL}/services/cap-cao-su`} element={<CapCaoSu />} />
                <Route path={`${process.env.PUBLIC_URL}/services/cap-chong-chay`} element={<CapChongChay />} />
                <Route path={`${process.env.PUBLIC_URL}/services/cap-dien-ap`} element={<CapDienAp />} />
                <Route path={`${process.env.PUBLIC_URL}/services/cap-dong-nhom-boc`} element={<CapDongNhomBoc />} />
                <Route path={`${process.env.PUBLIC_URL}/services/cap-dong-nhom-tran`} element={<CapDongNhomTran />} />
                <Route path={`${process.env.PUBLIC_URL}/services/cap-van-xoan`} element={<CapVanXoan />} />
                <Route path={`${process.env.PUBLIC_URL}/services/dien-dan-dung`} element={<DienDanDung />} />
                <Route path={`${process.env.PUBLIC_URL}/services/soi-nhom-tran`} element={<SoiNhomTran />} />
                <Route path={`${process.env.PUBLIC_URL}/service-details-left-sidebar`} element={<ServiceDetailsLeftSidebar />} />
                <Route path={`${process.env.PUBLIC_URL}/service-details-right-sidebar`} element={<ServiceDetailsRightSidebar />} />
                <Route path={`${process.env.PUBLIC_URL}/projects`} element={<Projects />} />
                <Route path={`${process.env.PUBLIC_URL}/project-details`} element={<ProjectDetails />} />
                <Route path={`${process.env.PUBLIC_URL}/blog-left-sidebar`} element={<BlogLeftSidebar />} />
                <Route path={`${process.env.PUBLIC_URL}/blog-right-sidebar`} element={<BlogRightSidebar />} />
                <Route path={`${process.env.PUBLIC_URL}/blog-details-left-sidebar`} element={<BlogDetailsLeftSidebar />} />
                <Route path={`${process.env.PUBLIC_URL}/blog-details-right-sidebar`} element={<BlogDetailsRightSidebar />} />
                <Route path={`${process.env.PUBLIC_URL}/contact-us`} element={<Contact />} />
                <Route path="*" element={<NoMAtch />} />
            </Routes>
        </Suspense>
      </Router>
  )
}

export default App
